* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
  background-color: #e3f1f1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  padding: 20px;
}

.main-container {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  gap: 40px;
  padding: 20px;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: auto;
  background-color: #d8eae8;
  border-radius: 20px;
}

.form-container {
  flex: 1;
  min-width: 350px;
  max-width: 500px;
  background-color: #fff;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.15);
  height: 100%;
}

.qr-container {
  flex: 1;
  min-width: 300px;
  max-width: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.15);
  height: 650px;
}

.qr-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 15px;
}

label {
  font-size: 14px;
  color: #555;
}

input[type="text"],
input[list],
select,
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  outline: none;
  transition: border-color 0.3s ease;
  background-color: #f9f9f9;
  height: 48px;
}

input[type="text"]:focus,
input[list]:focus,
select:focus,
textarea:focus {
  border-color: #66afe9;
  box-shadow: 0 0 5px rgba(102, 175, 233, 0.5);
}

/* Căn chỉnh hàng ngang cho hai ô input Nội dung */
.input-container {
  display: flex;
  gap: 10px;
}

.input-container input {
  flex: 1;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
}

.warning-message {
  color: red;
  font-size: 14px;
  font-weight: bold;
  margin-top: 5px;
  text-align: center;
}

button {
  width: 100%;
  padding: 14px;
  background: linear-gradient(90deg, #00b3a3 0%, #008f7a 100%);
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 16px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s ease;
  text-align: center;
  margin: 10px auto; /* Center the button on mobile */
}

button:hover {
  background: linear-gradient(90deg, #008f7a 0%, #00b3a3 100%);
}

#imageQR {
  max-width: 100%;
  height: 90%;
  display: block;
  margin: auto;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #f9f9f9;
}

#status {
  text-align: center;
  font-size: 16px;
  color: #28a745;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .main-container {
    flex-direction: column;
    align-items: center;
  }

  .form-container,
  .qr-container {
    width: 100%;
    max-width: 500px;
  }

  button {
    width: 100%;
  }
}

@media (max-width: 480px) {
  h2 {
    font-size: 20px;
  }
  input[type="text"],
  input[list],
  select,
  textarea {
    font-size: 14px;
    padding: 10px;
  }
  button {
    font-size: 14px;
    padding: 10px;
  }
  #imageQR {
    margin: 15px auto;
    max-width: 100%;
  }
}
